<template>
  <div class="search-box">
    <i class="icon-search"></i>
    <!---->
    <input class="box" v-model="query" :placeholder="placeholder" ref="input"/>
    <!---->
    <i class="icon-dismiss" v-if="query" @click="clearInputValue"></i>
  </div>
</template>

<script type="text/ecmascript-6">

export default {
  props: {
    placeholder: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: ''
    }
  },
  mounted () {
    this.$watch('query', this.jieLiuFunc((newQuery) => {
      // query search-result
      this.$emit('queryValue', newQuery)
    }, 300))
  },
  methods: {
    // 
    clearInputValue () {
      this.query = ''
    },
    // 
    andSearchToInput (value) {
      this.query = value
    },
    // 
    jieLiuFunc (func, time) {
      let timer
      return function (...args) {
        if (timer) {
          clearTimeout(timer)
        }
        timer = setTimeout(() => {
          func.apply(this, args)
        }, time)
      }
    },
    // 
    blurInput () {
      this.$refs.input.blur()
    }
  },
  watch: {
    // query () {
    //   // query search-result
    //   // querymontend
    //   this.$emit('queryValue', this.query)
    // }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"

  .search-box
    display: flex
    align-items: center
    box-sizing: border-box
    width: 100%
    padding: 0 6px
    height: 40px
    background: $color-highlight-background
    border-radius: 6px
    .icon-search
      font-size: 24px
      color: $color-background
    .box
      flex: 1
      margin: 0 5px
      line-height: 18px
      background: $color-highlight-background
      color: $color-text
      font-size: $font-size-medium
      &::placeholder
        color: $color-text-d
    .icon-dismiss
      font-size: 16px
      color: $color-background
</style>



