<template>
  <div ref="scrollWrap">
  <!---->
    <slot></slot>
</div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  props: {
    probeType: {// better-scroll
      type: Number,
      default: 1
    },
    click: {// better-scroll
      type: Boolean,
      default: true
    },
    data: {// ,
      type: Array,
      default: null
    },
    listenScroll: {// 
      type: Boolean,
      default: false
    },
    isShangLaLoading: {// 20true
      type: Boolean,
      default: false
    },
    isSearchResultMove: {// 
      type: Boolean,
      default: false
    },
    sleepRefresh: {//  datascrollrefresh
      type: Number,
      default: 20
    }
  },
  data() {
    return {
      message: ''
    }
  },
  mounted() {
    setTimeout(() => {
      // better-scrollDOM17
      if (!this.$refs.scrollWrap) {
        return
      }
      this.scroll = new BScroll(this.$refs.scrollWrap, {probeType: this.probeType, click: this.click})
      if (this.listenScroll) {
        let that = this // vue
        this.scroll.on('scroll', (position) => {
          that.$emit('zizujianscroll', position)
        })
      }
      // 20true
      if (this.isShangLaLoading) {
        let that = this // vue
        this.scroll.on('scrollEnd', () => {
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            console.log('')
            that.$emit('scrollToBottomLoadingMore')
          }
        })
      }
      // 
      if (this.isSearchResultMove) {
        let that = this // vue
        this.scroll.on('beforeScrollStart', () => {
          that.$emit('myBeforeScrollStart')
        })
      }
    }, 20)
  },
  methods: {
    enable() { // better-scroll
      this.scroll && this.scroll.enable()
    },
    disable() { // better-scroll
      this.scroll && this.scroll.disable()
    },
    refresh() { // better-scroll
      this.scroll && this.scroll.refresh()
    },
    scrollTo() { // better-scroll
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
  watch: {
    data() {
      // data
      setTimeout(() => {
        this.refresh()
      }, this.sleepRefresh)
    }
  }
}
</script>

<style scoped lang="stylus">
</style>



