import Vue from 'vue'
import Router from 'vue-router'

import rank from '../pages/rank/rank' // 
import rankDetail from '../pages/rank-detail/rank-detail' // 
import recommend from '../pages/recommend/recommend' // 
import search from '../pages/search/search' // 
import singer from '../pages/singer/singer' // 
import singerDetail from '../pages/singer-detail/singer-detail' // 
import recommendDetail from '../pages/recommend-detail/recommend-detail' // 
import userCenter from '../pages/user-center/user-center' // 
import loading from '../pages/loading/loading' // 

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/rank',
      component: rank,
      children: [
        {
          path: '/rank/:id',
          component: rankDetail
        }
      ]
    },
    {
      path: '/recommend',
      component: recommend,
      children: [
        {
          path: '/recommend/:id',
          component: recommendDetail
        }
      ]
    },
    {
      path: '/search',
      component: search,
      children: [
        {
          path: '/search/detail/:id',
          component: singerDetail
        }
      ]
    },
    {
      path: '/singer',
      component: singer,
      children: [
        {
          path: '/singer/detail/:id',
          component: singerDetail
        }
      ]
    },
    {
      path: '/user',
      component: userCenter
    },
    // {
    //   path: '/',
    //   redirect: '/recommend'
    // },
    {
      path: '/loading',
      component: loading
    },
    {
      path: '/',
      redirect: '/loading'
    },
    {
      path: '*',
      redirect: '/recommend'
    }
  ]
})



